/**
 * Styles for Ludvig's Custom Accordion block
 * Applies to both frontend and editor.
 */

.wp-block-ludvigs-custom-accordion {
	/* Block wrapper styles */
	margin: 2rem 0;
}

.accordion {
	border-radius: 8px;
	overflow: hidden;
	background: #fff;
}

.accordion-item {
	border-bottom: 1px solid #e0e0e0;
	background: #fff;
	transition: background 0.2s;
}
.accordion-item:last-child {
	border-bottom: none;
}
.accordion-item.open {
	background: #f7f7f7;
}

.accordion-header {
	cursor: pointer;
	padding: 1rem 1.5rem;
	user-select: none;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-weight: 600;
	font-size: 1.1rem;
	background: none;
	border: none;
	outline: none;
	color: #222;
	transition: background 0.2s, color 0.2s;
}
.accordion-header h3 {
	margin: 0;
	font-size: 1.1rem;
	color: inherit;
}


.accordion-toggle-indicator {
	margin-left: 1rem;
	width: 1.2em;
	height: 1.2em;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	color: #bbb;
	transition: transform 0.3s cubic-bezier(0.4,0,0.2,1), color 0.2s;
}
.accordion-toggle-indicator::before, .accordion-toggle-indicator::after {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	width: 1em;
	height: 2px;
	background: currentColor;
	border-radius: 1px;
	transition: inherit;
}
.accordion-toggle-indicator::before {
	transform: translate(-50%, -50%) rotate(0deg);
}
.accordion-toggle-indicator::after {
	transform: translate(-50%, -50%) rotate(90deg);
}
.accordion-item.open .accordion-toggle-indicator {
	color: #222;
	transform: rotate(45deg);
}

.accordion-content {
	max-height: 0;
	overflow: hidden;
	padding: 0 1.5rem;
	background: #fff;
	color: #222;
	font-size: 1rem;
	transition: max-height 0.3s cubic-bezier(0.4,0,0.2,1), padding 0.3s;
}
.accordion-item.open .accordion-content {
	padding: 1rem 1.5rem;
	max-height: 500px;
	transition: max-height 0.5s cubic-bezier(0.4,0,0.2,1), padding 0.3s;
}

.accordion-content p {
	margin: 0 0 0.5rem 0;
}

.accordion-content img {
	display: block;
	max-width: 100%;
	max-height: 180px;
	width: auto;
	height: auto;
	margin: 0.5rem 0 0.5rem 0;
	border-radius: 6px;
	object-fit: contain;
	border: 1px solid #e0e0e0;
}
